import { Button, Cell, DropdownMenu, Switch } from "@taroify/core"
import { View } from "@tarojs/components"
import * as React from "react"
import { Key, useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"

import "./index.scss"

function BasicDropdownMenu() {
  return (
    <DropdownMenu>
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
    </DropdownMenu>
  )
}

function DropdownMenuWithCustomContent() {
  const [value, setValue] = useState<Key | false>()
  const [option1, setOption1] = useState()
  const [switch1, setSwitch1] = useState(true)
  const [switch2, setSwitch2] = useState(false)
  return (
    <DropdownMenu value={value} onChange={setValue}>
      <DropdownMenu.Item value={option1} onChange={setOption1}>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
      <DropdownMenu.Item title="">
        <Cell title="" align="center">
          <Switch size="24" checked={switch1} onChange={setSwitch1} />
        </Cell>
        <Cell title="" align="center">
          <Switch size="24" checked={switch2} onChange={setSwitch2} />
        </Cell>
        <View style="padding: 5px 16px;">
          <Button color="danger" block shape="round" onClick={() => setValue(false)}>
            
          </Button>
        </View>
      </DropdownMenu.Item>
    </DropdownMenu>
  )
}

function DropdownMenuWithCustomColor() {
  return (
    <DropdownMenu className="custom-color">
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
    </DropdownMenu>
  )
}

function UpDropdownMenu() {
  return (
    <DropdownMenu direction="up">
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
    </DropdownMenu>
  )
}

function DisabledDropdownMenu() {
  return (
    <DropdownMenu>
      <DropdownMenu.Item disabled>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
      <DropdownMenu.Item disabled>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
    </DropdownMenu>
  )
}

export default function DropdownMenuDemo() {
  return (
    <Page className="dropdown-menu-demo" title="DropdownMenu ">
      <Block title="">
        <BasicDropdownMenu />
      </Block>
      <Block title="">
        <DropdownMenuWithCustomContent />
      </Block>
      <Block title="">
        <DropdownMenuWithCustomColor />
      </Block>
      <Block title="">
        <UpDropdownMenu />
      </Block>
      <Block title="">
        <DisabledDropdownMenu />
      </Block>
    </Page>
  )
}
